.full-height {
  min-height: calc(100vh - 80px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-link {
  cursor: pointer;
}

.show-feedback {
  display: block;
  margin-bottom: 15px;
}

.form-group {
  margin-bottom: 15px;
}

.form-control {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #ced4da;
  padding-left: 0;
}

.form-control:focus {
  box-shadow: none;
  border: none;
  border-bottom: 1px solid #80bdff;;
}

.form-spinner {
  color: #80bdff;
  margin-left: 10px;
}

.hide {
  opacity: 0;
  visibility: hidden;
}

.show {
  opacity:  1;
  visibility: visible;
}

.dropdown-menu {
  padding: 0;
}

.nav-link.dropdown-toggle {
  font-size: 14px;
}